@mixin border-radius($radius){
	-moz-border-radius: $radius;
	-webkit-border-radius:$radius;
	border-radius: $radius;
}

@mixin responsive-fonts($font-size){
	h1 {
		font-size: $font-size * 3
	}

	h2 {
		font-size: $font-size * 2
	}
	
	h3 {
		font-size: $font-size * 1.5
	}
	
	.btn-responsive {    
		font-size: $font-size ;    
	}

}